 class RouteConfig extends Component {
    render() {
        return (
            <BrowserRouter>
                <div className="route">
             route
                        <Route path="/" component={Index}></Route>
                </div>
            </BrowserRouter>
            
        )
    }
}
export default  RouteConfig 
