import React, { Component } from 'react'
import {hash} from '../util/index'


 class HeaderNav extends Component {
    render() {
        let {navList,user,tell}=this.props;
       
        return (
            <div className="headerNav">
                  <img src="/images/logo.png" alt=""/>
                 
                  <ul>
                      {
                          navList&&navList.map((item,index)=>{
                              console.log(hash())
                              return <li onClick={this.handleClick.bind(this,item)} key={hash()}>{item}</li>
                          })
                      }
                  </ul>
                   <div className="user">{user}</div>
                   <div className="info">{tell}</div>
            </div> 
        )
    }
    handleClick(item){
     console.log(this,'this',item)
    }
}
HeaderNav.defaultProps={
    navList:["首页",'员工管理',"员工考勤","工作计划","客户管理",'亚太学院',"级别考核","管理助手","积分管理"],
    user:"阳光",
    tell:'通知栏'

}
export default  HeaderNav;