import React, { Component } from 'react'
import HeaderNav from "./commonView/headerNav"
import Userinfo from "./commonView/userinfo"
import TipBlock from "./commonView/tipBlock"
class Index extends Component {
    render() {
        return (
            <div className="index">
                index
                
                <HeaderNav></HeaderNav>           
                <Userinfo></Userinfo>
                <TipBlock></TipBlock>
            </div>
        )
    }
}
export default  Index
